@import url('https://fonts.googleapis.com/css?family=Montserrat:400,800');

.container-wrapper {
    background-image: url('../background.jpg');
    font-family: 'Montserrat', sans-serif;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

.container-wrapper * {
    box-sizing: border-box;
}

.container-wrapper h1 {
    font-weight: bold;
    margin: 0;
}

.container-wrapper p {
    font-size: 14px;
    font-weight: 100;
    line-height: 20px;
    letter-spacing: 0.5px;
    margin: 20px 0 30px;
}

.container-wrapper span {
    font-size: 12px;
}

.container-wrapper a {
    color: #333;
    font-size: 14px;
    text-decoration: none;
    margin: 15px 0;
}

.container-wrapper .container {
    background: #fff;
    border-radius: 10px;
    box-shadow: 0 14px 28px rgba(0, 0, 0, 0.2), 0 10px 10px rgba(0, 0, 0, 0.2);
    position: relative;
    overflow: hidden;
    width: 768px;
    max-width: 100%;
    min-height: 480px;
}

.container-wrapper .form-container .data-form {
    background: #fff;
    display: flex;
    flex-direction: column;
    padding: 0 50px;
    height: 100%;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.container-wrapper .social-container {
    margin: 20px 0;
}

.container-wrapper .social-container a {
    border: 1px solid #ddd;
    border-radius: 50%;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    margin: 0 5px;
    height: 40px;
    width: 40px;
}

.container-wrapper .form-container input {
    background: #eee;
    border: none;
    padding: 12px 15px;
    margin: 8px 0;
    width: 100%;
}
.container-wrapper .form-container select {
    background: #eee;
    border: none;
    padding: 12px 15px;
    margin: 8px 0;
    width: 100%;
}

.container-wrapper button {
    border-radius: 20px;
    border: 1px solid #ff4b2b;
    background: #ff445c;
    color: #fff;
    font-size: 12px;
    font-weight: bold;
    padding: 12px 45px;
    letter-spacing: 1px;
    text-transform: uppercase;
    transition: transform 80ms ease-in;
}

.container-wrapper button:active {
    transform: scale(0.95);
}

.container-wrapper button:focus {
    outline: none;
}

.container-wrapper button.ghost {
    background: transparent;
    border-color: #fff;
}

.container-wrapper .form-container {
    position: absolute;
    top: 0;
    height: 100%;
    transition: all 0.6s ease-in-out;
}

.container-wrapper .sign-in-container {
    left: 0;
    width: 50%;
    z-index: 2;
}

.container-wrapper .sign-up-container {
    left: 0;
    width: 50%;
    z-index: 1;
    opacity: 0;
}

.container-wrapper .overlay-container {
    position: absolute;
    top: 0;
    left: 50%;
    width: 50%;
    height: 100%;
    overflow: hidden;
    transition: transform 0.6s ease-in-out;
    z-index: 100;
}

.container-wrapper .overlay {
    background: #ff416c;
    background: linear-gradient(to right, #ff4b2b, #ff416c) no-repeat 0 0 / cover;
    color: #fff;
    position: relative;
    left: -100%;
    height: 100%;
    width: 200%;
    transform: translateY(0);
    transition: transform 0.6s ease-in-out;
}

.container-wrapper .overlay-panel {
    position: absolute;
    top: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0 40px;
    height: 100%;
    width: 50%;
    text-align: center;
    transform: translateY(0);
    transition: transform 0.6s ease-in-out;
}

.container-wrapper .overlay-right {
    right: 0;
    transform: translateY(0);
}

.container-wrapper .overlay-left {
    transform: translateY(-20%);
}

/* Move signin to right */
.container-wrapper .right-panel-active .sign-in-container {
    transform: translateY(100%);
}

/* Move overlay to left */
.container-wrapper .right-panel-active .overlay-container {
    transform: translateX(-100%);
}

/* Bring signup over signin */
.container-wrapper .right-panel-active .sign-up-container {
    transform: translateX(100%);
    opacity: 1;
    z-index: 5;
}

/* Move overlay back to right */
.container-wrapper .right-panel-active .overlay {
    transform: translateX(50%);
}

/* Bring back the text to center */
.container-wrapper .right-panel-active .overlay-left {
    transform: translateY(0);
}

/* Same effect for right */
.container-wrapper .right-panel-active .overlay-right {
    transform: translateY(20%);
}

.container-wrapper .footer {
    margin-top: 25px;
    text-align: center;
}

.container-wrapper .icons {
    display: flex;
    width: 30px;
    height: 30px;
    letter-spacing: 15px;
    align-items: center;
}

@media (max-width: 768px) {
    .overlay-container{
        display: none;
    }
    .form-container .sign-up-container{
        display: none;
    }
        .container-wrapper .sign-in-container{
            width: 100%;
        }
        .create-account{
            margin-top: 9px;
        }
                                .container-wrapper .sign-up-container {
                                    left: 0;
                                    width: 100%;
                                    z-index: 1;
                                    opacity: 100;
                                }

}
@media (min-width: 768px) {
    .create-account {
       display: none !important;
        }
        
}
